import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {WimaxServiceProvider} from "../../providers/wimax-service/wimax-service";



@IonicPage()
@Component({
  selector: 'page-wimax-recharge03',
  templateUrl: 'wimax-recharge03.html',
})
export class WimaxRecharge03Page {

  accountNo;
  provider;
  packageType;
  cardDetails;
  wimaxRechargeError03 = "";
  min = 0;
  max = 5;
  totalAmount = 0;
  rdAmount = true;
  amountDisabled = true;
  amountStyle = "";
  pinCode = "";
  wUser = "";
  rAmount = "";
  rType = 0;
  totAmount = 0;
  wimaxUser;


  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public tokenService: TokenServiceProvider,
              public storage: LocalStorageProvider,
              public maskTokenUtil: MaskTokenProvider,
              public cryptoUtil: CryptoParamUtilProvider,
              public wimaxService: WimaxServiceProvider) {

    this.accountNo = this.navParams.get("accountNo");
    this.provider = this.navParams.get("provider");
    this.packageType = this.navParams.get("packageType");
    this.cardDetails = JSON.parse(this.navParams.get("cardDetails"));
    this.initPage();
  }

  initPage = function () {

    if (this.provider.toLowerCase() == 'qubee' && this.packageType == "postpaid") {

      this.rdAmount = false;
      this.amountDisabled = false;
      this.amountStyle = "";
      this.rType = 1;
    }
    else {

      this.rdAmount = true;
      this.amountDisabled = true;
      this.amountStyle = "border: 0px solid #505050;";
      this.rType = 0;
    }
  };

  IncrementVal = function (listItem) {
    if (listItem.quantity < this.max) {
      listItem.quantity = listItem.quantity + 1;
      this.totalAmount = this.totalAmount + parseInt(listItem.cardValue);
    }
    else {
    }
  };

  DecrementVal = function (listItem) {
    if (listItem.quantity > this.min) {
      listItem.quantity = listItem.quantity - 1;
      this.totalAmount = this.totalAmount - parseInt(listItem.cardValue);
    }
    else {

    }
  };

  WimaxRecharge03Action = function (totalAmount, wimaxUser, tpin) {
    this.wimaxRechargeError03 = "";

    let validateTpinExp = new RegExp("[0-9]{4}$");
    let flag = true;

    if (totalAmount == "" || typeof(totalAmount) == "undefined") {
      this.wimaxRechargeError03 = "Recharge Amount can not be blank."
      flag = false;
      return;
    }


    if (wimaxUser == "" || typeof(wimaxUser) == "undefined") {
      this.wimaxRechargeError03 = "Service Account ID can not be blank."
      flag = false;
      return;
    }

    if (tpin == "" || typeof(tpin) == "undefined") {
      this.wimaxRechargeError03 = "TPIN can not be blank."
      flag = false;
      return;
    }

    if (!validateTpinExp.test(tpin)) {
      this.wimaxRechargeError03 = "Invalid TPIN."
      flag = false;
      return;
    }

    if (totalAmount > 9999) {
      this.wimaxRechargeError03 = "Minimum recharge amount 9999 taka.";
      flag = false;
      return;
    }

    if (flag) {
      this.pinCode = tpin;
      this.wUser = wimaxUser;
      this.totAmount = totalAmount;
      if (this.rType == 1) {
        this.rAmount = totalAmount;
      }

      else {
        let firstLoop = 0;
        Object.keys(this.cardDetails).forEach(key => {
          let value = this.cardDetails[key];
          if (value.quantity > 0) {
            if (firstLoop == 0) {
              this.rAmount = value.cardValue + "@" + value.quantity;
            }
            else {
              this.rAmount = this.rAmount + "#" + value.cardValue + "@" + value.quantity;
            }
            firstLoop++;
          }
        })
      }

      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let sProvider = this.provider.toLowerCase();
      let encProvider = this.cryptoUtil.getEncryptVal(sProvider);
      let encPackage = this.cryptoUtil.getEncryptVal(this.packageType);
      let encTPin = this.cryptoUtil.getEncryptVal(this.pinCode);
      let encWimaxUser = this.cryptoUtil.getEncryptVal(this.wUser);
      let encAccNo = this.cryptoUtil.getEncryptVal(this.accountNo);
      let encOrder = this.cryptoUtil.getEncryptVal(this.rAmount);
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.wimaxService.makeWimaxRecharge(unMaskToken, appData.userId, appData.appId, encProvider, encPackage, encTPin, encWimaxUser, encOrder, encAccNo, encAccNo)
        .then(data => {
          this.onWimaxRechargeComplete(data);
        });
    }
  };

  onWimaxRechargeComplete = function (data) {
    if (data.statusCode == 1) {
      this.navCtrl.push('WimaxRecharge04Page', {
        'refID': data.object.wimaxPayRefId,
        'amount': this.totAmount
      });
    }

    else if (data.statusCode == 0) {
      this.wimaxRechargeError03 = data.note;
    }

    else {
      this.wimaxRechargeError03 = "System Error Contact with Administrator.";
    }
  };

  ionViewDidLoad() {

  }

}



